// @flow
import { stableSort, getComparator } from 'helpers/Table';

export const DOCUMENTS_LIMITATION = 3;

export const getLatestDocs = (documents: Array<Object>): Array<Object> => {
  const sortedDocuments = stableSort(
    documents,
    getComparator('desc', 'EventTime'),
  );
  return sortedDocuments.slice(0, DOCUMENTS_LIMITATION);
};
