// @flow
import clsx from 'clsx';
import React, { useEffect } from 'react';
import type { AbstractComponent, Element } from 'react';
import { Link as Router } from 'react-router-dom';
import BlockUi from 'react-block-ui';
import { Loader } from 'react-loaders';
import { hideIfInvalidProps } from 'utils/RenderComponent';
import { Typography, List, ListItem, Link } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import _isEmpty from 'lodash/isEmpty';
import _get from 'lodash/get';
import { ListItemIcon } from '@material-ui/core';
import DescriptionOutlinedIcon from '@material-ui/icons/DescriptionOutlined';
import { useDownloadDocs } from '../../hooks';
import { DOCUMENTS_LIMITATION, getLatestDocs } from './helper';
import { APP_PATHS } from 'shared/Consts';
import { toastr } from 'react-redux-toastr';

const useStyles = makeStyles(theme => ({
  link: {
    ...theme.typography.h5,
    display: 'flex',
    color: theme.palette.text.dark,
  },
  list: {
    marginLeft: '0px',
  },
  listItem: {
    padding: '4px 0',
  },
  listItemIcon: {
    minWidth: '34px',
    '& path': {
      fill: theme.palette.common.gray,
    },
  },
  title: {
    ...theme.typography.h6,
    color: theme.palette.text.secondary,
    flex: 1,
  },
  linkHighlight: {
    fontWeight: '900',
  },
}));

const checkValidProps = ({ data }) => !_isEmpty(data);

type Props = {
  data: Object,
  isSeller: boolean,
};

export const DocumentItem = (props: Props): Element<typeof BlockUi> => {
  const { isSeller, data } = props;
  const classes = useStyles();
  const { doDownloadDocs, isDownloading, error } = useDownloadDocs();

  useEffect(() => {
    const { message } = error || {};
    if (message) {
      toastr.error('Something went wrong', message);
    }
  }, [error]);

  const title = isSeller
    ? _get(data, 'PropertyData.FullAddress')
    : _get(data, 'OfferNo');
  const documents = _get(data, 'Docs');

  const handleDownloadDocs = (objectKeys: Array<string>) => {
    doDownloadDocs(objectKeys);
  };

  const hasMore = documents.length > DOCUMENTS_LIMITATION;
  const sortedDocuments = getLatestDocs(documents);

  return (
    <BlockUi
      tag="div"
      blocking={isDownloading}
      loader={<Loader active type="ball-pulse" color="rgba(231,28,99,1)" />}
      className={clsx(classes.container, {
        [classes.isDownloading]: isDownloading,
      })}
    >
      <Typography className={classes.title}>{title}</Typography>
      <List className={classes.list}>
        {sortedDocuments.map(document => {
          const { FileId: fileId, ObjectKey: objectKey, File: file } = document;
          return (
            <ListItem key={fileId} className={classes.listItem}>
              <ListItemIcon className={classes.listItemIcon}>
                <DescriptionOutlinedIcon />
              </ListItemIcon>
              <Link
                className={classes.link}
                href="#"
                onClick={() => handleDownloadDocs([objectKey])}
              >
                {_get(file, 'name', '')}
              </Link>
            </ListItem>
          );
        })}
        {hasMore && (
          <ListItem key="more-documents" className={classes.listItem}>
            <ListItemIcon className={classes.listItemIcon}>
              <DescriptionOutlinedIcon />
            </ListItemIcon>
            <Router to={APP_PATHS.documents}>
              <Link className={classes.link} component="span">
                ...
              </Link>
            </Router>
          </ListItem>
        )}
      </List>
    </BlockUi>
  );
};

export default (hideIfInvalidProps(checkValidProps)(
  DocumentItem,
): AbstractComponent<Props>);
