// @flow
import { makeStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import _isEmpty from 'lodash/isEmpty';
import React, { Fragment, useContext } from 'react';
import type { Element } from 'react';
import BlockUi from 'react-block-ui';
import { Loader } from 'react-loaders';
import { withErrorBoundary } from 'components/ErrorBoundary';
import UserContext from 'contexts/UserContext';
import { Widget } from 'components/Widget';
import DocumentItem from './DocumentItem';
import { prepareDocumentsData } from 'components/Documents/helpers';

const useStyles = makeStyles(theme => ({
  container: {
    minHeight: '100px',
  },
  loading: {
    border: '1px solid #E0E0E0',
    backgroundColor: theme.palette.common.white,
  },
}));

type Props = {
  data: Array<Object>,
  isLoading: Boolean,
};

export const DocumentWidget = ({
  isLoading,
  data,
}: Props): Element<'div' | typeof Fragment> => {
  const classes = useStyles();
  const { IsSeller: isSeller } = useContext(UserContext);

  const documents = prepareDocumentsData({
    rawDocumentData: data,
    isSeller,
    filterOutEmpty: true,
  });

  if ((_isEmpty(data) || _isEmpty(documents)) && !isLoading) {
    return <></>;
  }

  return (
    <BlockUi
      tag="div"
      blocking={isLoading}
      loader={<Loader active type="ball-pulse" color="rgba(231,28,99,1)" />}
      className={clsx(classes.container, { [classes.loading]: isLoading })}
    >
      <Widget title="Documents">
        {Object.keys(documents).map(key => {
          return (
            <DocumentItem key={key} data={documents[key]} isSeller={isSeller} />
          );
        })}
      </Widget>
    </BlockUi>
  );
};

export default (withErrorBoundary(DocumentWidget): Element<Props>);
